/** styles the popup box **/
.containerpopup {
  border: 1px solid black;
  width: 200px;
  height: 375px;
  box-shadow: 3px 3px 5px 6px #ccc;
  font-size: 12px;
  padding: 5px 5px;
  background: white;
  position: absolute;
  top: 10px;
  right: 20px;
  z-index: 200;
}

/** class to make position relative **/
.relative {
  position: relative;
}

/** class to change color for selected day tab **/
.selected {
  background-color: #47FF38;
}

/** style the information icon **/
#infoIcon {
  width: 10px;
  height: 10px;
  display: inline-block;
  margin-left: 10px;
}

/** style the information box popup **/
#informationBox {
  border: 1px solid black;
  width: 35vw;
  padding: 0px 10px;
  background-color: white;
  font-size: 10.5px;
  z-index: 100;
}

/** style the checkboxes for the repeat options **/
.checkbox-container {
  width: 45%;
  display: inline-block;
  margin-right: 1%;
  height: 75px;
}

.right {
  float: right;
}
