/** style the information icon **/
#infoIconRemote {
  width: 10px;
  height: 10px;
  display: inline-block;
  margin-left: 10px;
}

/** style the information box popup **/
#informationBoxRemote {
  border: 1px solid black;
  width: 35vw;
  padding: 0px 10px;
  background-color: white;
  font-size: 10.5px;
  z-index: 200;
}



/** style the information icon **/
#infoIconPresence {
  width: 10px;
  height: 10px;
  display: inline-block;
  margin-left: 10px;
}

/** style the information box popup **/
#informationBoxPresence {
  border: 1px solid black;
  width: 35vw;
  padding: 10px;
  background-color: white;
  font-size: 10.5px;
  text-align: left;
}

#informationBoxAdjustedPresence {
  border: 1px solid black;
  width: 35vw;
  padding: 10px;
  background-color: white;
  font-size: 10.5px;
  text-align: left;
}


/** position the popups **/
#DesktopPopupPresence{
  z-index:300
}

#MonitorPopupPresence {
  z-index:300
}

#LaptopPopupPresence {
  z-index: 300;
}

#TaskLampPopupPresence {
  z-index: 300;
}

#FanPopupPresence {

  z-index: 300;
}

/** class for popup when hidden **/
.invisiblePresence {
  display: none;
}

/** class for popup when shown **/
.visiblePresence {
  border: 1px solid #707070;
  box-shadow: 3px 3px 5px 6px #ccc;
  padding: 15px 5px;
  text-align: center;
  width: 125px;
  height: 65px;
  margin-left: 10px;
}
