/** style the final component that includes the icon, name, and dropdown **/

/** position the dropdown **/
.dropdown {
  border: 1px solid white;
  height: 75px;
  margin-left: 100px;
}

/** style the box **/
.containerPresence {
  height: 10rem;
  padding-top: 1rem;
  padding-bottom: 1rem;
  padding-left: 1rem;
  vertical-align: bottom;
  margin-bottom: 10px;
  display: inline-block;
  margin-right: 20px;
}

/** position the icon and name **/
.iconPresence {
  float: left;
}
